import React from "react";
import {
  Dialog,
  DialogContent,
  makeStyles,
  Typography,
} from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import { setFiatDialog } from "../../../../redux/reducers/replenishment";

const DialogConfirm = ({ t }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const { fiat, replenishmentData } = useSelector(
    ({ replenishmentReducer }) => replenishmentReducer
  );

  return (
    <Dialog
      open={fiat.isConfrirmDialogOpen && replenishmentData?.data?.confirm}
      onClose={() => {
        dispatch(setFiatDialog({ ...fiat, isConfrirmDialogOpen: false }));
      }}
      scroll="body"
      classes={{ paper: classes.paper }}
    >
      <DialogContent className={classes.DialogContent}>
        <Typography variant="h2" className={classes.title}>
          {t("paymentСonfirmation")}
        </Typography>
        <ul style={{ listStyle: "none" }}>
          <Typography variant="h5" component="li">
            {t("amountToBeCredited")}
            <div></div>
            <span>
              {replenishmentData?.data?.confirm?.debit}{" "}
              {replenishmentData?.data?.confirm?.currency}
            </span>
          </Typography>
          <Typography variant="h5" component="li">
            {t("amountToPay")}
            <div></div>
            <span>
              {replenishmentData?.data?.confirm?.amount}{" "}
              {replenishmentData?.data?.confirm?.currency}
            </span>
          </Typography>
          <Typography variant="h5" component="li">
            {t("ourCommission")}
            <div></div>
            <span>
              {replenishmentData?.data?.confirm?.commission}{" "}
              {replenishmentData?.data?.confirm?.currency}
            </span>
          </Typography>
          <Typography variant="h5" component="li">
            {t("note")}:<div></div>
            <span>{replenishmentData?.data?.confirm?.description}</span>
          </Typography>
        </ul>
        <div
          dangerouslySetInnerHTML={{
            __html: replenishmentData?.data?.form,
          }}
        />
      </DialogContent>
    </Dialog>
  );
};

export default DialogConfirm;

const useStyles = makeStyles((theme) => ({
  paper: {
    borderRadius: 16,
    width: "100%",
  },

  DialogContent: {
    padding: "40px 31px!important",
    display: "flex",
    justifyContent: "center",
    flexDirection: "column",

    [theme.breakpoints.down(992)]: {
      padding: "16px",
    },

    "& ul": {
      "& li": {
        fontWeight: 300,
        marginBottom: 16,
        display: "flex",
        justifyContent: "space-between",
        whiteSpace: "nowrap",
        alignItems: "baseline",

        "& div": {
          width: "100%",
          height: "fit-content",
          borderBottom: "2px dotted #B8C0CC",
          margin: "0 3px",
        },

        "& span": {
          fontWeight: 500,
        },
      },
    },

    "& button": {
      width: "100%",
      height: 56,
      marginTop: 16,
      background: "#FCE34D",
      borderRadius: "8px",
      border: "none",
      fontSize: 18,
      fontWeight: 500,
    },
  },

  close: {
    position: "absolute",
    top: 0,
    right: 0,
  },

  title: {
    textAlign: "center",
    color: "#000",
    fontWeight: 500,
    letterSpacing: "-0.04em",
    lineHeight: "56px",
    marginBottom: 24,

    [theme.breakpoints.down(992)]: {
      fontSize: 48,
    },

    [theme.breakpoints.down(768)]: {
      fontSize: 34,
      lineHeight: "40px",
    },
  },

  subtitle: {
    color: "#64748B",
    marginLeft: 10,
  },
}));
