/* eslint-disable eqeqeq */
import {
  Button,
  Dialog,
  DialogContent,
  IconButton,
  makeStyles,
  Typography,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setInfoDialog } from "../../../redux/reducers/replenishment";
import { Swiper, SwiperSlide } from "swiper/react";

import SwiperCore, { Pagination } from "swiper";
import close from "../../../assets/images/state/close.svg";
import { MediumTitle } from "../../../shared/Typography";
import { useLocation } from "react-router";
import { Box } from "@mui/system";

SwiperCore.use([Pagination]);

const DialogInfo = ({ t }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const location = useLocation();

  const { info, replenishmentMethods } = useSelector(
    ({ replenishmentReducer }) => replenishmentReducer
  );

  const [choosenTerminal, setChoosenTerminal] = useState(null);

  useEffect(() => {
    const choosenTerminal = Object.values(
      typeof replenishmentMethods?.data?.methods === "object" &&
        replenishmentMethods?.data?.methods
    )?.find((e) => e.name == info.name);

    setChoosenTerminal(choosenTerminal);
    // eslint-disable-next-line
  }, [info]);

  return (
    <Dialog
      open={info.open}
      onClose={() => dispatch(setInfoDialog({ ...info, open: false }))}
      scroll="body"
      classes={{ paper: classes.paper }}
    >
      <DialogContent className={classes.DialogContent}>
        <IconButton
          aria-label="close"
          onClick={() => dispatch(setInfoDialog({ ...info, open: false }))}
          className={classes.close}
        >
          <img src={close} alt="" />
        </IconButton>
        {location.pathname === "/dashboard/replenish/cash" ? (
          <>
            <Typography variant="h2" className={classes.title}>
              {t("ForPayment")} "{info.name}"
            </Typography>

            <Typography variant="h5" style={{ fontWeight: 300 }}>
              {choosenTerminal?.info?.description}
            </Typography>
            <Box
              sx={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                marginTop: "24px",
              }}
            >
              <Typography className={classes.wa}>
                {choosenTerminal?.info?.phone}
              </Typography>
            </Box>
          </>
        ) : (
          <>
            <Typography variant="h2" className={classes.title}>
              {t("topUpVia")} "{info.name}"
            </Typography>

            <Swiper>
              {choosenTerminal?.info?.photos?.map((item) => (
                <SwiperSlide>
                  <img src={item} className={classes.img} alt="" />
                </SwiperSlide>
              ))}
            </Swiper>

            <MediumTitle className={classes.subtitle}>
              {t("ToPayThroughTheTerminal")} "{info.name}"
            </MediumTitle>

            <ul
              dangerouslySetInnerHTML={{
                __html: choosenTerminal?.info?.description,
              }}
            />
          </>
        )}
        <Button
          variant="contained"
          color="secondary"
          className={classes.button}
          onClick={() => dispatch(setInfoDialog({ ...info, open: false }))}
        >
          {t("clear")}
        </Button>
      </DialogContent>
    </Dialog>
  );
};

export default DialogInfo;

const useStyles = makeStyles((theme) => ({
  paper: {
    borderRadius: 16,
    width: "100%",

    "& ul": {
      fontSize: 20,
      listStyle: "none",
      marginTop: 24,
      whiteSpace: "prewrap",
      fontWeight: 300,
      marginBottom: 10,
      "&:last-child": {
        marginBottom: 0,
      },
    },
  },

  DialogContent: {
    padding: "40px 31px!important",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",

    [theme.breakpoints.down(992)]: {
      padding: "16px",
    },
  },

  close: {
    position: "absolute",
    top: 0,
    right: 0,
  },

  title: {
    textAlign: "center",
    color: "#000",
    fontWeight: 500,
    letterSpacing: "-0.04em",
    lineHeight: "56px",
    marginBottom: 24,

    [theme.breakpoints.down(992)]: {
      fontSize: 48,
    },

    [theme.breakpoints.down(768)]: {
      fontSize: 34,
      lineHeight: "40px",
    },
  },

  button: {
    height: 56,
    width: "100%",
    marginTop: 32,
  },

  wa: {
    color: "#1BD741",
    fontSize: 39,
    fontWeight: 500,
  },

  subtitle: {
    magrin: "32px 0 24px",
    paddingTop: 32,
    borderTop: "1px solid #E7EAEE",
  },

  img: {
    maxWidth: "65%",
    [theme.breakpoints.down(840)]: {
      maxWidth: "85%",
    },
    [theme.breakpoints.down(600)]: {
      maxWidth: "85%",
    },
    [theme.breakpoints.down(550)]: {
      maxWidth: "80%",
    },
    [theme.breakpoints.down(450)]: {
      maxWidth: "75%",
    },
    [theme.breakpoints.down(350)]: {
      maxWidth: "70%",
    },
  },
}));
