import React, { useState } from "react";
import {
  Button,
  CircularProgress,
  Dialog,
  DialogContent,
  IconButton,
  makeStyles,
  styled,
  Typography,
} from "@material-ui/core";
import { Box } from "@mui/system";
import { useDispatch, useSelector } from "react-redux";
import {
  getReplenishmentData,
  setCryptoDialog,
  setLoading,
} from "../../../../redux/reducers/replenishment";
import { MediumTitle } from "../../../../shared/Typography";
import { SwipeableDrawer } from "@mui/material";
import { CopyToClipboard } from "react-copy-to-clipboard";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";
import warning from "../../../../assets/images/state/warning.svg";
import copyIcon from "../../../../assets/images/state/document-copy.svg";
import close from "../../../../assets/images/state/close.svg";
const Content = ({
  classes,
  replenishmentData,
  isLoading,
  t,
  crypto,
  setCopy,
  copy,
  drawer,
}) => {
  const dispatch = useDispatch();
  return (
    <>
      <IconButton
        aria-label="close"
        onClick={() => {
          dispatch(setCryptoDialog({ ...crypto, open: false }));
        }}
        className={classes.close}
      >
        <img src={close} alt="" />
      </IconButton>

      {isLoading ? (
        <Box display="flex" alignItems="center" justifyContent="center">
          <CircularProgress disableShrink color="secondary" />
        </Box>
      ) : (
        <>
          <Typography
            variant="h2"
            style={{ marginTop: drawer && 48 }}
            className={classes.title}
          >
            {crypto.name} {t("depositAddress")}
          </Typography>
          <Box display="flex" alignItems="center">
            <img src={warning} style={{ marginRight: 17 }} alt="" />
            <Typography variant="h5" className={classes.warning}>
              {t("DialogCryptoSubtitle")}
            </Typography>
          </Box>
          <img
            src={replenishmentData?.data?.qrcode_url}
            className={classes.img}
            alt=""
          />
          <MediumTitle
            style={{
              wordBreak: "break-all",
            }}
          >
            {crypto.name === "Tether"
              ? `Tether TRC20 ${t("Address")}`
              : `${crypto.name} ${t("Address")}`}
          </MediumTitle>
          <CopyToClipboard text={replenishmentData?.data?.deposit_address}>
            <Box
              sx={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                mb: 2,
              }}
            >
              <MediumTitle className={classes.mediumTitle}>
                {replenishmentData?.data?.deposit_address}
              </MediumTitle>
              <LightTooltip title="Скопировано" open={copy} placement="top-end">
                <IconButton
                  style={{ marginLeft: 16 }}
                  onClick={() => setCopy(true)}
                  onMouseLeave={() => setCopy(false)}
                >
                  <img src={copyIcon} alt="" />
                </IconButton>
              </LightTooltip>
            </Box>
          </CopyToClipboard>
          <Button
            onClick={() => {
              dispatch(setLoading(true));
              dispatch(
                getReplenishmentData("cryptocurrency", {
                  method: crypto.name,
                  renew: "1",
                })
              );
            }}
            variant="contained"
            size="large"
          >
            <svg
              style={{ marginRight: 16 }}
              width="15"
              height="15"
              viewBox="0 0 15 15"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M14 7.5C14 11.0927 11.0927 14 7.5 14C3.90727 14 1.72089 10.3836 1.72089 10.3836M1.72089 10.3836H4.65181M1.72089 10.3836V13.6336M1 7.5C1 3.90727 3.88364 1 7.5 1C11.8373 1 14 4.61636 14 4.61636M14 4.61636V1.36636M14 4.61636H11.1164"
                stroke="black"
                stroke-width="1.5"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
            </svg>
            {t("generateNew")}
          </Button>
        </>
      )}
    </>
  );
};

const DialogCrypto = ({ t }) => {
  const { crypto, replenishmentData, isLoading } = useSelector(
    ({ replenishmentReducer }) => replenishmentReducer
  );

  const dispatch = useDispatch();
  const classes = useStyles();
  const [copy, setCopy] = useState(false);

  return (
    <Dialog
      open={crypto.open}
      classes={{ paper: classes.paper }}
      onClose={() => {
        dispatch(setCryptoDialog({ ...crypto, open: false }));
        dispatch(setLoading(true));
      }}
      scroll="body"
    >
      <DialogContent className={classes.dialog}>
        <Content
          classes={classes}
          replenishmentData={replenishmentData}
          isLoading={isLoading}
          t={t}
          crypto={crypto}
          setCopy={setCopy}
          copy={copy}
        />{" "}
      </DialogContent>
    </Dialog>
  );
};

export default DialogCrypto;

const useStyles = makeStyles((theme) => ({
  paper: {
    width: "100%",
    borderRadius: 16,
    padding: "40px 19px 50px!important",
  },
  dialog: {
    overflow: "hidden",
    display: "flex",
    maxWidth: "73%",
    margin: "0 auto",
    padding: "0!important",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",

    [theme.breakpoints.down(992)]: {
      padding: "16px",
    },
  },
  warning: {
    color: "#F59E0B",
    fontWeight: "300",
    marginTop: 24,

    [theme.breakpoints.down(992)]: {
      fontSize: 20,
    },

    [theme.breakpoints.down(768)]: {
      fontSize: 16,
      lineHeight: "19px",
      marginTop: 16,
    },
  },

  img: {
    width: 180,
    height: 180,
    display: "block",
    margin: "40px auto",

    [theme.breakpoints.down(768)]: {
      margin: "16px auto",
    },
  },

  mediumTitle: {
    color: "#64748B",
    marginTop: 16,
    textOverflow: "ellipsis",
    overflow: "hidden",
    maxWidth: "90%",
    wordBreak: "break-word",

    [theme.breakpoints.down(768)]: {
      marginTop: 8,
      fontSize: 16,
    },
  },

  close: {
    position: "absolute",
    top: 0,
    right: 0,
  },

  title: {
    textAlign: "center",
    color: "#000",
    fontWeight: 500,
    letterSpacing: "-0.04em",
    lineHeight: "56px",
    wordBreak: "break-word",

    [theme.breakpoints.down(992)]: {
      fontSize: 48,
    },

    [theme.breakpoints.down(768)]: {
      fontSize: 34,
      lineHeight: "40px",
    },
  },
}));

export const DrawerReplenish = ({ t }) => {
  const { crypto, replenishmentData, isLoading } = useSelector(
    ({ replenishmentReducer }) => replenishmentReducer
  );
  const classes = useStyles();
  const [copy, setCopy] = useState(false);

  const dispatch = useDispatch();
  return (
    <SwipeableDrawer
      anchor="right"
      sx={{
        "& .MuiPaper-root": {
          width: { xs: "100%", lg: "30%" },
          p: 2,
        },
      }}
      open={crypto.open}
      onClose={() => {
        dispatch(setCryptoDialog({ ...crypto, open: false }));
        dispatch(setLoading(true));
      }}
      // onOpen={() => dispatch(setCryptoDialog({ ...crypto, open: true }))}
    >
      <Content
        classes={classes}
        replenishmentData={replenishmentData}
        isLoading={isLoading}
        t={t}
        crypto={crypto}
        setCopy={setCopy}
        copy={copy}
        drawer
      />
    </SwipeableDrawer>
  );
};

const LightTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(({ theme }) => ({
  [`& .${tooltipClasses.tooltip}`]: {
    fontSize: 14,
    fontWeight: 400,
  },
}));
