import { Grid, Snackbar, useMediaQuery, useTheme } from "@material-ui/core";
import React, { useEffect } from "react";
import Card from "../Card";
import { useDispatch, useSelector } from "react-redux";
import { Alert } from "@mui/material";
import { getReplenishmentMethods } from "../../../../redux/reducers/replenishment";
import CryptoSkeleton from "../../../../components/CryptoSkeleton";

const Fiat = () => {
  const theme = useTheme();
  const tablet = useMediaQuery(theme.breakpoints.down(992));
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getReplenishmentMethods("fiat"));
    // eslint-disable-next-line
  }, []);

  const { replenishmentMethods, replenishmentData, isLoadingMethods } =
    useSelector(({ replenishmentReducer }) => replenishmentReducer);

  return (
    <Grid
      container
      spacing={2}
      style={!tablet ? { width: "75%" } : { width: "100%", marginTop: 24 }}
    >
      <Snackbar
        autoHideDuration={100}
        open={replenishmentData?.data?.messages}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert severity="error" color="error" variant="filled">
          {replenishmentData?.data?.messages}
        </Alert>
      </Snackbar>
      {isLoadingMethods
        ? Array.from(Array(4).keys()).map((item, idx) => (
            <Grid item xs={12} sm={6} md={6} lg={4} xl={4}>
              <CryptoSkeleton key={idx} style={{ width: 389 }} />
            </Grid>
          ))
        : Object.values(
            typeof replenishmentMethods.data?.methods === "object" &&
              replenishmentMethods.data?.methods
          ).map((item) => (
            <Grid item xs={12} sm={6} md={6} lg={4} xl={4}>
              <Card item={item} fiat />
            </Grid>
          ))}
    </Grid>
  );
};

export default Fiat;
