import { makeStyles, Typography } from "@material-ui/core"
import { Box } from "@mui/system"
import React from "react"
import { useDispatch } from "react-redux"
import { setFiatDialog, setInfoDialog } from "../../../redux/reducers/replenishment"
import { WhiteBox } from "../../../shared/Boxes"

const Card = ({ item, terminals, cash }) => {
	const classes = useStyles()
	const dispatch = useDispatch()

	return (
		<WhiteBox
			className={classes.box}
			onClick={() => {
				if (!terminals && !cash) {
					dispatch(
						setFiatDialog({
							open: true,
							name: item.currency,
							cash: item.name,
							type: item.name,
							currency: item.currency,
							commission: item.commission,
							category: item.category,
							slug: item.slug,
						}),
					)
				} else {
					return dispatch(setInfoDialog({ open: true, name: item.name }))
				}
			}}
		>
			<img src={item.logo} alt="" />
			<Box className={classes.textBox}>
				<Typography className={classes.title}>{item.currency}</Typography>
				<Typography className={classes.subtitle}>{item.name}</Typography>
			</Box>
			<Box className={classes.saleBlock}>
				<Typography variant="body1">{item.commission}%</Typography>
			</Box>
		</WhiteBox>
	)
}

export default Card

const useStyles = makeStyles((theme) => ({
	box: {
		padding: "17px 0 0 0",
		height: 194,
		display: "flex",
		justifyContent: "space-between",
		alignItems: "center",
		flexDirection: "column",
		cursor: "pointer",
		"& img": {
			width: 64,
			height: 64,
			objectFit: "contain",
		},
	},
	title: {
		fontSize: 33,
		fontWeight: "500",
		lineHeight: "40px",
		color: "#000000",
		marginBottom: 4,
		textOverflow: "ellipsis",
		overflow: "hidden",
		whiteSpace: "nowrap",
		maxWidth: "100%",
		textAlign: "center",

		[theme.breakpoints.down(992)]: {
			fontSize: 30,
		},
	},

	textBox: {
		overflow: "hidden",
		maxWidth: "90%",
	},

	subtitle: {
		color: "#64748B",
		fontSize: 16,
		lineHeight: "19px",
		textOverflow: "ellipsis",
		overflow: "hidden",
		maxWidth: "100%",
		textAlign: "center",
		whiteSpace: "nowrap",
	},

	saleBlock: {
		width: "100%",
		maxHeight: 35,
		background: "#0D0F11",
		textAlign: "center",
		borderRadius: "0 0 8px 8px",
		marginTop: 8,
		padding: "6px 0",
	},
}))
