import React, { useLayoutEffect, useState } from "react";
import {
  Button,
  CircularProgress,
  Dialog,
  DialogContent,
  IconButton,
  makeStyles,
  TextField,
  Typography,
} from "@material-ui/core";
import { Box } from "@mui/system";
import { Form, Formik } from "formik";
import { useDispatch, useSelector } from "react-redux";
import {
  getReplenishmentData,
  setFiatDialog,
} from "../../../../redux/reducers/replenishment";
import close from "../../../../assets/images/state/close.svg";
import commissions from "../../../../assets/images/state/commisison.svg";

const DialogFiat = ({ t }) => {
  const dispatch = useDispatch();
  const classes = useStyles();

  const { fiat, replenishmentData } = useSelector(
    ({ replenishmentReducer }) => replenishmentReducer
  );

  const [loading, setLoading] = useState(false);

  const firstUpdate = React.useRef(true);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    if (replenishmentData.status === 200) {
      dispatch(
        setFiatDialog({
          ...fiat,
          open: false,
          isConfrirmDialogOpen: true,
        })
      );
    }
    setLoading(false);
    // eslint-disable-next-line
  }, [replenishmentData]);

  return (
    <Dialog
      open={fiat.open}
      onClose={() => dispatch(setFiatDialog({ ...fiat, open: false }))}
      scroll="body"
      classes={{ paper: classes.paper }}
    >
      <DialogContent className={classes.DialogContent}>
        <Formik
          initialValues={{
            sum: "",
          }}
          onSubmit={(values) => {
            setLoading(true);
            dispatch(
              getReplenishmentData(fiat.category, {
                sum: values.sum,
                method: fiat.slug,
              })
            );
          }}
        >
          {({ errors, handleChange, values }) => (
            <Form className={classes.foorm}>
              <IconButton
                aria-label="close"
                onClick={() =>
                  dispatch(setFiatDialog({ ...fiat, open: false }))
                }
                className={classes.close}
              >
                <img src={close} alt="" />
              </IconButton>
              <Typography variant="h2" className={classes.title}>
                {t("topUpVia")} "{fiat.type}"
              </Typography>
              <Box display="flex" alignItems="center" mb={"28px"}>
                <img src={commissions} alt="" />
                <Typography variant="h5" className={classes.subtitle}>
                  {t("commissions")} {fiat.commission}%
                </Typography>
              </Box>

              <TextField
                type="number"
                required
                className={classes.formControl}
                label={`${t("enterTheAmount")} ${fiat.currency}`}
                variant="outlined"
                fullWidth
                name="sum"
                value={values.sum}
                onChange={handleChange}
              />

              <Button
                variant="contained"
                type="submit"
                disabled={loading || !values.sum}
                className={classes.button}
                color="primary"
              >
                {loading ? (
                  <CircularProgress color="secondary" />
                ) : (
                  t("TopUpBalance")
                )}
              </Button>
            </Form>
          )}
        </Formik>
      </DialogContent>
    </Dialog>
  );
};

export default DialogFiat;

const useStyles = makeStyles((theme) => ({
  paper: {
    borderRadius: 16,
    width: "100%",
    backgroundColor: "#F7F8F9!important",
  },

  DialogContent: {
    padding: "40px 31px!important",
    display: "flex",
    justifyContent: "center",

    [theme.breakpoints.down(992)]: {
      padding: "16px",
    },
  },
  foorm: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
    maxWidth: 584,

    "& ul": {
      "& li": {
        fontWeight: 300,
        marginBottom: 16,
        display: "flex",
        justifyContent: "space-between",

        "& span": {
          fontWeight: 500,
        },
      },
    },
  },

  form: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
    maxWidth: 384,
  },

  button: {
    width: "100%",
    height: 56,
    marginTop: 16,
  },

  close: {
    position: "absolute",
    top: 0,
    right: 0,
  },

  title: {
    textAlign: "center",
    color: "#000",
    fontWeight: 500,
    letterSpacing: "-0.04em",
    lineHeight: "56px",
    marginBottom: 24,

    [theme.breakpoints.down(992)]: {
      fontSize: 48,
    },

    [theme.breakpoints.down(768)]: {
      fontSize: 34,
      lineHeight: "40px",
    },
  },

  formControl: {
    backgroundColor: "#FFF",
    boxShadow: "0px 1px 0px #E2E8F0",
  },

  subtitle: {
    color: "#64748B",
    marginLeft: 10,
  },
}));
