import {
  Container,
  Grid,
  makeStyles,
  Typography,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import { Route, useLocation } from "react-router";
import { MediumTitle } from "../../../shared/Typography";
import Navigation from "./Navigation";

import Crypto from "./Crypto";
import Terminals from "./Terminals";
import Fiat from "./Fiat";
import Cash from "./Cash";
import DialogFiat from "./Fiat/DialogFiat";
import DialogInfo from "./DialogInfo";
import Balance from "../../../components/Balance";
import { useEffect } from "react";
import { getReplenishmentCategories } from "../../../redux/reducers/replenishment";
import { useDispatch } from "react-redux";
import DialogConfirm from "./Fiat/DialogConfirm";
import { getСurrenciesList } from "../../../redux/reducers/Сurrencies";

const Replenish = ({ t, match }) => {
  const theme = useTheme();
  const tablet = useMediaQuery(theme.breakpoints.down(992));
  const classes = useStyles();
  const location = useLocation();
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getReplenishmentCategories());
    dispatch(getСurrenciesList());
    // eslint-disable-next-line
  }, []);

  return (
    <section className="replenish">
      <Container maxWidth="lg">
        <Balance />
        <MediumTitle className={classes.title}>{t("TopUpBalance")}</MediumTitle>
        {location.pathname !== "/dashboard/replenish/crypto" && (
          <Typography variant="body1" className={classes.subtitle}>
            {t("replenishmentSubtitle")}
          </Typography>
        )}
        <Grid
          container
          spacing={2}
          justifyContent={!tablet ? "space-between" : "center"}
          alignItems={!tablet ? "start" : "center"}
          direction={!tablet ? "row" : "column"}
        >
          <Grid
            item
            style={!tablet ? { width: "25%", padding: 0 } : { width: "100%" }}
          >
            <Navigation t={t} />
          </Grid>
          <Route
            exact
            path="/dashboard/replenish/cryptocurrency"
            render={() => <Crypto t={t} />}
          />
          <Route
            exact
            path="/dashboard/replenish/terminals"
            render={() => <Terminals />}
          />
          <Route
            exact
            path="/dashboard/replenish/fiat"
            render={() => <Fiat />}
          />
          <Route
            exact
            path="/dashboard/replenish/cash"
            render={() => <Cash />}
          />

          <DialogFiat t={t} />
          <DialogInfo t={t} />
          <DialogConfirm t={t} />
        </Grid>
      </Container>
    </section>
  );
};

export default Replenish;

const useStyles = makeStyles((theme) => ({
  title: {
    marginBottom: 24,
    [theme.breakpoints.down(992)]: {
      marginBottom: 16,
    },
  },

  subtitle: {
    color: "#323A46",
    marginBottom: 24,
    fontWeight: "300",
  },
}));
