import { Box } from "@mui/system";
import React from "react";

import { makeStyles, Typography } from "@material-ui/core";
import { WhiteBox } from "../../../../shared/Boxes";
import { useDispatch } from "react-redux";
import {
  getReplenishmentData,
  setCryptoDialog,
} from "../../../../redux/reducers/replenishment";

const Card = ({ item }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  return (
    <WhiteBox
      className={classes.box}
      onClick={() => {
        dispatch(
          setCryptoDialog({
            open: true,
            name: item.name,
            currency: item.currency,
          })
        );
        dispatch(getReplenishmentData(item.category, { method: item.slug }));
      }}
    >
      <img src={item.logo} alt="" />
      <Box className={classes.textBox}>
        <Typography className={classes.title}>{item.currency}</Typography>
        <Typography className={classes.subtitle}>{item.name}</Typography>
      </Box>
    </WhiteBox>
  );
};

export default Card;

const useStyles = makeStyles((theme) => ({
  box: {
    cursor: "pointer",
    "& img": {
      objectFit: "contain",
      width: 64,
      height: 64,
      marginRight: 16,

      [theme.breakpoints.down(992)]: {
        marginRight: 10,
      },

      [theme.breakpoints.down(576)]: {
        marginBottom: 15,
      },
    },
  },

  textBox: {
    maxWidth: "77%",
    overflow: "hidden",
  },

  title: {
    fontSize: 33,
    fontWeight: "500",
    lineHeight: "40px",
    color: "#000000",
    marginBottom: 4,
    textOverflow: "ellipsis",
    overflow: "hidden",
    whiteSpace: "nowrap",
    maxWidth: "100%",

    [theme.breakpoints.down(992)]: {
      fontSize: 30,
    },
  },

  subtitle: {
    color: "#64748B",
    fontSize: 16,
    lineHeight: "19px",
    textOverflow: "ellipsis",
    overflow: "hidden",
    maxWidth: "100%",

    [theme.breakpoints.down(576)]: {
      textAlign: "center",
    },
  },
}));
